.navbar {
  position: fixed;
  top: 0;
  width: 100%;
  background-color: #333;
  overflow: hidden;
  align-items: center;
  text-align: center;
  z-index: 1000; /* Ensure the navbar is above other content */
}

.navbar a {
  display: inline-flex;
  color: white;
  text-align: center;
  padding: 14px 20px;
  text-decoration: none;
}

.navbar a:hover {
  background-color: #ddd;
  color: black;
}

.nav-links {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.nav-links li {
  display: inline;
}

.footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  background-color: rgba(51, 51, 51, 0.5);
  color: #fff;
  text-align: center;
  padding: 8px 0;
}





